import React, { Suspense } from "react";
import "./App.css";
import "bootstrap/dist/css/bootstrap.min.css";
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";
import Store from "./store";
import { Provider } from "react-redux";

import ScrollToTop from "./utils/ScrollToTop";
const Home = React.lazy(() => import("./views/Home"));
const Portfolios = React.lazy(() => import("./views/Portfolios"));
const Blog = React.lazy(() => import("./views/Blog"));
const Components = React.lazy(() => import("./views/Components"));
const Contacts = React.lazy(() => import("./views/Contacts"));
const Login = React.lazy(() => import("./views/Login"));
const Signup = React.lazy(() => import("./views/Signup"));
const Security = React.lazy(() => import("./views/Security"));
const ChangePassword = React.lazy(() => import("./views/ChangePassword"));
const Profile = React.lazy(() => import("./views/Profile"));
const ProtectedPage = React.lazy(() =>
  import("./shared-components/ProtectedPage")
);
const ProfileServices = React.lazy(() =>
  import("./shared-components/ProfileServices")
);

function App() {
  return (
    <Provider store={Store}>
      <div className="App">
        <Router>
          <ScrollToTop />
          <Routes>
            <Route
              path="/"
              exact
              element={
                <Suspense fallback={<div>Loading...</div>}>
                  <Home />
                </Suspense>
              }
            />
            <Route
              path="/home"
              element={
                <Suspense fallback={<div>Loading...</div>}>
                  <Home />
                </Suspense>
              }
            />
            <Route
              path="/portfolios"
              element={
                <Suspense fallback={<div>Loading...</div>}>
                  <Portfolios />
                </Suspense>
              }
            />
            <Route
              path="/blog"
              element={
                <Suspense fallback={<div>Loading...</div>}>
                  <Blog />
                </Suspense>
              }
            />
            <Route
              path="/components"
              element={
                <Suspense fallback={<div>Loading...</div>}>
                  <Components />
                </Suspense>
              }
            />
            <Route
              path="/contact"
              element={
                <Suspense fallback={<div>Loading...</div>}>
                  <Contacts />
                </Suspense>
              }
            />
            <Route
              path="/login"
              element={
                <Suspense fallback={<div>Loading...</div>}>
                  <Login />
                </Suspense>
              }
            />
            <Route
              path="/profile"
              element={
                <ProtectedPage>
                  <Suspense fallback={<div>Loading...</div>}>
                    <Profile />
                  </Suspense>
                </ProtectedPage>
              }
            />
            <Route
              path="/profile/services"
              element={
                <ProtectedPage>
                  <Suspense fallback={<div>Loading...</div>}>
                    <ProfileServices />
                  </Suspense>
                </ProtectedPage>
              }
            />
            <Route
              path="/signup"
              element={
                <Suspense fallback={<div>Loading...</div>}>
                  <Signup />
                </Suspense>
              }
            />
            <Route
              path="/security"
              element={
                <Suspense fallback={<div>Loading...</div>}>
                  <Security />
                </Suspense>
              }
            />
            <Route
              path="/change_password"
              element={
                <Suspense fallback={<div>Loading...</div>}>
                  <ChangePassword />
                </Suspense>
              }
            />
            <Route
              path="*"
              element={
                <Suspense fallback={<div>Loading...</div>}>
                  <Home />
                </Suspense>
              }
            />
          </Routes>
        </Router>
      </div>
    </Provider>
  );
}

export default App;
